/********* HEADER *********/
@use "abstract/" as *;
@use "../vendors/hamburger" as *;
@use "../vendors/navClose" as *;
@use "../vendors/navEffect" as *;
.customNav {
  max-width: 120rem;
  margin-top: 3.2rem;
  nav {
    background: linear-gradient(17.68deg, #ffffff 22.98%, rgba(255, 255, 255, 0) 311.68%);
    border-radius: 8px;
    height: 9.8rem;
    padding: 1.2rem;
  }
  &__logo {
    a {
      img {
        max-width: 17.4rem;
      }
    }
  }
  .nav-item {
    margin-right: 6.4rem;
    &:last-child {
      margin-right: 0;
    }
  }
  .nav-link {
    text-transform: uppercase;
    color: $theme--blue !important;
    font: 1.6rem/1.8rem $karla-bold, sans-serif !important;
  }
  &-rightCol {
    display: flex;
  }
  &-socialMedia {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 15rem;
    li {
      margin-right: 2rem;
      &:last-child {
        margin-right: 0;
      }
    }
    a {
      max-width: 2.8rem;
      img {
        width: 100%;
      }
    }
  }
}

.active--state {
  border-bottom: 2px solid #346ca4;
}
@include media(1199px) {
  header .container {
    max-width: 96rem !important;
  }
}
@include media(991px) {
  div#navbarSupportedContent {
    position: absolute;
    top: 9.8rem;
    text-align: center;
    background: linear-gradient(17.68deg, #ffffff 22.98%, rgba(255, 255, 255, 0) 311.68%);
    border: 1px solid rgba(63, 65, 69, 0.01);
    border-radius: 4px;
    width: 100%;
    left: 0;
    border-top: 2px solid $theme--blue;
  }
  .customNav .nav-item {
    margin: 1.6rem 0;
  }
  .customNav-socialMedia {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1.6rem;
  }
}
