/********* FOOTER *********/
@use "abstract/" as *;
footer {
  background: linear-gradient(171.78deg, #346ca4 38.04%, rgba(66, 131, 197, 0.58) 159.79%);
  ul {
    li {
      margin-bottom: 0.8rem;
      h6 {
        font: 700 2.4rem/2.8rem $raleway-bold, sans-serif;
        color: $white--color;
        margin-bottom: 0.8rem;
      }
      p,
      a {
        font: 500 1.6rem/3.2rem $sen-regular, sans-serif;
        color: $white--color;
      }
      a:hover {
        text-decoration: none;
        color: $white--color;
      }
    }
  }
  .followUs {
    &-icons {
      display: flex;
    }
  }
  .aboutUs,
  .contact {
    flex-basis: 25%;
    max-width: 27.5rem;
  }
  .quickLink,
  .followUs {
    flex-basis: 25%;
    max-width: 20rem;
  }
}
.footerWrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 8rem;
  padding-bottom: 8rem;
}
@include media(1199px) {
  footer {
    .aboutUs {
      flex-basis: 100%;
    }
    .quickLink,
    .followUs,
    .contact {
      flex-basis: 40%;
    }
    .aboutUs,
    .quickLink,
    .followUs,
    .contact {
      margin-bottom: 2.4rem;
    }
  }
  footer .aboutUs,
  footer .contact {
    max-width: 100%;
  }
}
@include media(767px) {
  footer {
    text-align: center;
    .aboutUs,
    .quickLink,
    .followUs,
    .contact {
      flex-basis: 100%;
    }
    .followUs-icons {
      justify-content: center;
    }
  }
  footer .followUs,
  footer .quickLink {
    max-width: 100%;
  }
}
@include media(575px) {
  .footerWrap {
    padding: 4rem 0;
  }
  footer {
    text-align: center;
    .aboutUs,
    .quickLink,
    .followUs,
    .contact {
      flex-basis: 100%;
      max-width: 100%;
    }
  }
}
#copyright {
  background-color: $theme--blue;
  padding: 1.6rem 0;
  h6 {
    font: 1.6rem $raleway-bold, sans-serif;
    color: $white--color;
    text-align: center;
  }
}
