@use "abstract/" as *;
@use "../vendors/timeline" as *;
/** Banner **/
.banner {
  background: url("../img/bg.png") no-repeat right;
  height: 70rem;
  background-size: cover;
  .container {
    max-width: 120rem;
  }
  &-txtWrap {
    max-width: 65.3rem;
    p {
      font: 3.2rem/4rem $raleway-semibold, sans-serif;
      color: $white--color;
      margin-bottom: 2.4rem;
    }
    h1 {
      font: 6.4rem/8rem $raleway-bold, sans-serif;
      color: $white--color;
      max-width: 44.4rem;
    }
  }
}
@include respond-between(1200px, 1300px) {
  .banner .container {
    max-width: 114rem;
  }
}
@include media(1199px) {
  .banner .container {
    max-width: 96rem;
  }
}
@include media(767px) {
  .banner-txtWrap p {
    margin-bottom: 2rem;
    font: 2.8rem/2rem $raleway-semibold, sans-serif;
  }
}
@include media(575px) {
  .banner-txtWrap p {
    font: 2.8rem/4rem $raleway-semibold, sans-serif;
  }
  .banner-txtWrap h1 {
    font: 4.2rem/5rem $raleway-bold, sans-serif;
  }
  .banner {
    height: 58rem;
    background-position: 78% 50%;
  }
  .banner-txtWrap {
    margin-top: 6rem;
  }
}
/** About **/
.about {
  padding: 6.4rem 0;
  .container {
    .row:first-child {
      margin-bottom: 6.4rem;
    }
  }
  &ImgWrap {
    max-width: 45rem;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      border: 8px solid $theme--blue;
      height: 100%;
      width: 100%;
      top: 4rem;
      left: 4rem;
      z-index: -1;
    }
    img {
      width: 100%;
    }
  }
  &Head {
    &-txtWrap {
      position: relative;
      margin-left: 5.6rem;
      padding-top: 6.4rem;
      span {
        position: absolute;
        text-transform: uppercase;
        -webkit-text-stroke: 1px rgba(0, 0, 0, 0.1);
        color: $white--color;
        font: 16rem/13.6rem $raleway-bold, sans-serif;
        z-index: -1;
        top: 0.1rem;
        left: -4rem;
      }
      h2 {
        font: 8.8rem/9.2rem $raleway-bold, sans-serif;
        color: $theme--dark--blue;
        text-transform: capitalize;
        margin-bottom: 3rem;
      }
    }
  }
  &-bodyTxts {
    margin-left: 5.6rem;
    p {
      font: 1.6rem/3.2rem $sen-regular, sans-serif;
      color: $theme--light--black;
      margin-bottom: 2.4rem;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__lgTxt {
    font: 1.6rem/3.2rem $sen-regular, sans-serif;
    color: $theme--light--black;
    margin-bottom: 2.4rem;
    &:first-child {
      margin-top: 4rem;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}
@include media(1400px) {
  .aboutHead-txtWrap span {
    font-size: 12.9rem;
    left: -2rem;
  }
  .aboutHead-txtWrap,
  .about-bodyTxts {
    margin-left: 7.2rem;
  }
}
@include media(1199px) {
  .aboutHead-txtWrap span {
    font-size: 13rem;
    top: 0.1rem;
    left: -2rem;
  }
  .aboutImgWrap {
    max-width: 38rem;
  }
  .about .container .row:first-child {
    margin-bottom: 2.4rem;
  }
  .about__lgTxt:first-child {
    margin-top: 0;
  }
}
@include media(991px) {
  .aboutImgWrap {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 8.8rem;
  }
  .aboutHead {
    margin-top: 3rem;
    margin-left: 0;
  }
  .aboutHead-txtWrap,
  .about-bodyTxts {
    margin-left: 0;
  }
}
@include media(575px) {
  .aboutHead-txtWrap span {
    font-size: 9rem;
    left: -0.6rem;
  }
  .aboutHead-txtWrap h2 {
    font-size: 6.4rem;
  }
  .aboutImgWrap:after {
    top: 4rem;
    left: 0;
  }
  .aboutImgWrap {
    margin-bottom: 6rem;
  }
}
/** Icons Sec **/
.light--bg {
  background: $icon--light--bg;
  padding: 4.8rem 1.7rem;
}
.icon {
  &BoxWrap {
    display: flex;
    justify-content: space-evenly;
    margin-left: auto;
    margin-right: auto;
  }
  &Box {
    max-width: 26.2rem;
    max-height: 23.2rem;
    padding: 3rem;
    background: $white--color;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.38);
    border-radius: 1.8rem;
    &-img {
      max-width: 9.2rem;
      max-height: 9.2rem;
      height: 100%;
      margin-bottom: 1.6rem;
      img {
        width: 100%;
      }
    }
    &-txts {
      margin-bottom: 1.6rem;
      p {
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        font: 1.5rem/3.2rem $sen-regular, sans-serif;
        color: $theme--black;
      }
      &__readMore {
        font: 1.5rem/3.2rem $sen-regular, sans-serif;
        color: $read--more--blue;
        float: right;
      }
      &__readless {
        @extend .iconBox-txts__readMore;
      }
    }
  }
}
@include lgmedia(1200px) {
  .iconBox {
    max-width: 22rem;
  }
}
@include lgmedia(1400px) {
  .iconBox {
    max-width: 25rem;
  }
}
@include lgmedia(1440px) {
  .iconBoxWrap {
    max-width: 140rem;
  }
}
@include media(1199px) {
  .icon--mb--0 {
    margin-bottom: 0 !important;
  }
  .icon--mr--0 {
    margin-right: 0 !important;
  }
  .iconBoxWrap {
    flex-wrap: wrap;
    justify-content: center;
  }
  .iconBox {
    flex-basis: 50%;
    max-width: 44rem;
    margin-right: 2.5rem;
    margin-bottom: 2.5rem;
  }
}
@include media(991px) {
  .iconBox {
    flex-basis: 100%;
    min-width: 50rem;
    margin-right: 0;
  }
}
@include media(575px) {
  .iconBox {
    margin-right: 0;
    min-width: 30rem;
  }
}
/** Icons Sec Highlight**/
.light--white--bg {
  background: #f5faff;
  padding-top: 4rem;
  padding-bottom: 4rem;
}
.iconSec-highlight-img {
  position: absolute;
  top: -4.2rem;
  margin-left: auto;
  margin-right: auto;
  display: block;
  max-width: 120rem;
}
.iconSec {
  &-highlight {
    max-width: 120rem;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    &::after {
      position: absolute;
      content: "";
      background: url("../img/svg/pattern.svg") repeat-x;
      background-size: contain;
      height: 8rem;
      width: 100%;
      top: -4rem;
      max-width: 120rem;
    }
    h3 {
      font: 4.1rem/7.2rem $raleway-semibold, sans-serif;
      color: $theme--dark--blue;
      span {
        color: rgba(0, 115, 213, 0.51);
        font: 2rem/3.2rem $sen-regular, sans-serif;
        margin-right: 18rem;
      }
    }
    h4 {
      position: absolute;
      top: 50%;
      z-index: 0;
      left: 50%;
      transform: translate(-50%, -50%);
      font: 16rem/12.5rem $raleway-semibold, sans-serif;
      color: rgba(0, 115, 213, 0.05);
      -webkit-text-stroke: 1px rgba(0, 115, 213, 0.07);
    }
  }
}
@include media(1300px) {
  .iconSec-highlight {
    max-width: 114rem;
  }
}
@include media(1199px) {
  .iconSec-highlight {
    max-width: 96rem;
  }
  .iconSec-highlight h4 {
    font-size: 10rem;
  }
}
@include media(991px) {
  .iconSec-highlight {
    max-width: 72rem;
  }
  .iconSec-highlight h3 {
    font-size: 4rem;
  }
}
@include media(767px) {
  .iconSec-highlight {
    max-width: 54rem;
  }
  .iconSec-highlight h4 {
    font-size: 7rem;
  }
  .iconSec-highlight h3 {
    font: 3rem/6rem $raleway-semibold, sans-serif;
  }
  .iconSec-highlight:after {
    display: none;
  }
}
@include media(575px) {
  .iconSec-highlight {
    max-width: 100%;
  }
  .iconSec-highlight h4 {
    font: 4rem/12.5rem $raleway-semibold, sans-serif;
  }
  .light--white--bg {
    padding-top: 1.6rem;
    padding-bottom: 1.6rem;
  }
  .iconSec-highlight h3 {
    font-size: 2.4rem;
  }
  .iconSec-highlight h3 span {
    display: block;
  }
}
/** Sec About **/
.sec-about {
  padding: 6.4rem 0;
  .container {
    .row:first-child {
      margin-bottom: 6.4rem;
    }
  }
  &ImgWrap {
    max-width: 45rem;
    position: relative;
    margin-left: auto;
    &::after {
      content: "";
      position: absolute;
      border: 8px solid $theme--blue;
      height: 100%;
      width: 100%;
      top: 0;
      left: -4rem;
      z-index: -1;
    }
    img {
      width: 100%;
      padding-top: 4rem;
      padding-bottom: 4rem;
    }
  }
  &-bodyTxts {
    margin-left: 5.6rem;
    p {
      font: 1.6rem/3.2rem $sen-regular, sans-serif;
      color: $theme--light--black;
      margin-bottom: 2.4rem;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
@include media(1400px) {
  .sec-about-bodyTxts {
    margin-left: 7.2rem;
  }
}
@include media(1199px) {
  .sec-aboutImgWrap {
    max-width: 38rem;
  }
  .sec-about .container .row:first-child {
    margin-bottom: 0;
  }
}
@include media(991px) {
  .sec-aboutImgWrap {
    margin-left: auto;
    margin-right: auto;
    margin-top: 6.4rem;
  }
  .sec-about-bodyTxts {
    margin-left: 0;
  }
  .sec-aboutImgWrap {
    max-width: 45rem;
  }
}
@include media(575px) {
  .sec-aboutImgWrap:after {
    top: 0;
    left: 0;
  }
  .sec-aboutImgWrap {
    margin-bottom: 0rem;
  }
  .sec-aboutImgWrap {
    margin-top: 3.2rem;
  }
  .sec-aboutImgWrap img {
    padding-top: 0;
  }
}
/**Features**/
.features {
  .container:first-child {
    position: relative;
    &::before {
      background: #e5f2ff;
      position: absolute;
      content: "";
      height: 37rem;
      left: -68rem;
      width: 100%;
      top: 0;
    }
  }
  .container:nth-child(2) .row {
    margin-bottom: 14rem;
    &:last-child {
      margin-bottom: 6.4rem;
    }
  }
  &--lightBg {
    background: #e5f2ff;
    border-radius: 0 6rem 6rem 0;
    padding: 10.4rem 0 7.6rem;
    margin-left: unset;
    margin-right: auto;
    margin-bottom: 14rem;
  }
  &-mainHead-txtWrap {
    max-width: 79.6rem;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    .first__span {
      position: absolute;
      text-transform: uppercase;
      -webkit-text-stroke: 1px rgba(0, 0, 0, 0.1);
      color: rgba(255, 255, 255, 0);
      font: 16rem/13.6rem $raleway-bold, sans-serif;
      top: -4rem;
      left: -4rem;
    }
    h5 {
      font: 8.8rem/9.2rem $raleway-bold, sans-serif;
      color: $theme--dark--blue;
      position: relative;
      z-index: 1;
      &::after {
        position: absolute;
        content: "";
        background: rgba(86, 155, 224, 0.6);
        width: 13.9rem;
        height: 5px;
        border-radius: 5px;
        bottom: -3.2rem;
        right: 0;
      }
      span {
        margin-left: 5.6rem;
        strong {
          font: 8.8rem/9.2rem $sen-bold, sans-serif;
          font-weight: bold;
          letter-spacing: 1px;
        }
      }
    }
  }
  &-imgWrap {
    max-width: 64rem;
    position: relative;
    img {
      width: 100%;
    }
    &-Num {
      border-radius: 50%;
      border: 1px solid $theme--light--black;
      width: 12rem;
      height: 12rem;
      position: absolute;
      top: -10rem;
      left: -1.5rem;
      h5 {
        font: 6.4rem/6.6rem $poppins-bold, sans-serif;
        color: rgba(18, 23, 48, 0.8);
        display: flex;
        align-items: center;
        height: 100%;
        justify-content: center;
      }
    }
  }
  &Head-txtWrap {
    max-width: 48rem;
    position: relative;
    margin-bottom: 4rem;
    margin-left: auto;
    margin-right: auto;
    span {
      position: absolute;
      text-transform: uppercase;
      -webkit-text-stroke: 1px rgba(0, 0, 0, 0.1);
      color: $white--color;
      font: 9.6rem/11.2rem $raleway-bold, sans-serif;
      z-index: -1;
      top: 0.1rem;
      width: 100%;
      text-align: center;
    }
    h5 {
      font: 5.6rem/6.8rem $raleway-bold, sans-serif;
      color: $theme--dark--blue;
      text-transform: capitalize;
      padding-top: 4.8rem;
      text-align: center;
      position: relative;
      max-width: 39.8rem;
      margin-left: auto;
      margin-right: auto;
      &::after {
        position: absolute;
        content: "";
        background: rgba(229, 242, 255, 1);
        width: 13.9rem;
        height: 5px;
        border-radius: 5px;
        bottom: -2.2rem;
        right: 4rem;
      }
    }
  }
  &-bodyTxts {
    @extend .about-bodyTxts;
  }
}
@include lgmedia(1400px) {
  .features--lightBg {
    max-width: 130rem;
  }
}
@include media(1199px) {
  .features-bodyTxts {
    margin-left: 0;
  }
  .features-imgWrap {
    margin-left: auto;
    margin-right: auto;
  }
}
@include media(991px) {
  .features-mainHead-txtWrap h5,
  .features-mainHead-txtWrap h5 span strong {
    font-size: 7.2rem;
  }
  .features-mainHead-txtWrap h5 span {
    display: block;
  }
  .features-mainHead-txtWrap .first__span {
    font-size: 12rem;
  }
  .features-mainHead-txtWrap {
    max-width: 65.3rem;
  }
  .features-mainHead-txtWrap .first__span {
    left: -3rem;
  }
}
@include media(767px) {
  .features-mainHead-txtWrap h5,
  .features-mainHead-txtWrap h5 span strong {
    font-size: 5.6rem;
    line-height: 7.2rem;
  }
  .features-mainHead-txtWrap .first__span {
    font-size: 9rem;
  }
  .features-mainHead-txtWrap {
    max-width: 49.8rem;
    padding-right: 15px;
    padding-left: 15px;
  }
  .features-mainHead-txtWrap h5 span {
    margin-left: 0;
  }
  .features-mainHead-txtWrap .first__span {
    left: -2rem;
  }
  .features-mainHead-txtWrap h5:after {
    bottom: -1.6rem;
  }
  .features .container:first-child:before {
    height: 33rem;
    left: -54rem;
  }
}
@include media(575px) {
  .features--lightBg {
    padding: 5.6rem 0;
  }
  .features-mainHead-txtWrap .first__span {
    left: 0.7rem;
    top: -5.1rem;
  }
  .features-mainHead-txtWrap .first__span {
    font-size: 5.6rem;
  }
  .features-mainHead-txtWrap h5,
  .features-mainHead-txtWrap h5 span strong {
    font-size: 3.2rem;
    line-height: 5rem;
  }
  .features-mainHead-txtWrap h5:after {
    width: 6.9rem;
    right: 0.8rem;
  }
  .features-mainHead-txtWrap {
    max-width: 33.6rem;
  }
  .features .container:first-child:before {
    height: 20rem;
  }
  .featuresHead-txtWrap span {
    font: 5.6rem/11.2rem $raleway-bold, sans-serif;
    left: 0;
  }
  .featuresHead-txtWrap h5 {
    font: 4rem/5.6rem $raleway-bold, sans-serif;
  }
  .features .mt--64 {
    margin-top: 1.6rem;
  }
  .features-imgWrap-Num {
    top: -7rem;
    left: -0.5rem;
    width: 8rem;
    height: 8rem;
  }
  .features-imgWrap-Num h5 {
    font-size: 4.8rem;
  }
  .features--lightBg {
    margin-bottom: 12rem;
  }
}
/** Choose Us **/
.blue--line {
  position: relative;
  &::before {
    position: absolute;
    content: "";
    background-color: $theme--dark--blue;
    width: 600px;
    height: 2px;
    border-radius: 50%;
    left: -62.1rem;
    top: 4.6rem;
  }
}
.chooseUs {
  padding: 6.4rem 0;
  background: url("../img/choose-bg.png") no-repeat center;
  background-size: cover;
  height: 68.5rem;
  &__headTxt {
    font: 6.4rem/9.2rem $raleway-bold, sans-serif;
    color: $theme--dark--blue;
    margin-bottom: 6.4rem;
    position: relative;
    &::after {
      position: absolute;
      content: "";
      background-color: $theme--dark--blue;
      width: 4rem;
      height: 2px;
      border-radius: 50%;
      left: -4.5rem;
      top: 4.6rem;
    }
  }
  &-icon {
    &Wrap {
      display: flex;
      flex-wrap: wrap;
    }
    &Box {
      max-width: 41rem;
      display: flex;
      align-items: flex-start;
      margin-right: 2.8rem;
      flex-basis: 30%;
      margin-bottom: 3.2rem;
      &-img {
        margin-right: 2.4rem;
        img {
          width: 9.4rem;
        }
      }
      &-txts {
        h6 {
          font: small-caps 2.8rem/3.1rem $raleway-bold, sans-serif;
          color: rgba(34, 34, 34, 0.75);
          margin-bottom: 1.6rem;
          text-transform: capitalize;
        }
        p {
          font: 2rem/3.4rem $sen-regular, sans-serif;
          color: $theme--light--black;
        }
      }
    }
  }
}
@include media(1199px) {
  .chooseUs {
    height: 100%;
  }
  .chooseUs-iconBox {
    flex-basis: 50%;
    margin-bottom: 5.6rem;
  }
}
@include media(991px) {
  .chooseUs-iconBox {
    flex-basis: 100%;
    margin-left: auto;
    margin-right: auto;
    max-width: 60rem;
  }
}
@include media(767px) {
  .chooseUs__headTxt {
    font: 5.6rem/6.7rem $raleway-bold, sans-serif;
  }
  .blue--line:before,
  .chooseUs__headTxt:after {
    top: 3.6rem;
  }
}
@include media(575px) {
  .chooseUs__headTxt {
    font: 5.8rem/6.7rem $raleway-bold, sans-serif;
  }
  .chooseUs-iconBox-img img {
    width: 5.6rem;
  }
  .chooseUs-iconBox-txts h6 {
    font-size: 2.4rem;
  }
  .chooseUs-iconBox-txts p {
    font-size: 1.6rem;
  }
  .blue--line:before,
  .chooseUs__headTxt:after {
    display: none;
  }
}
@include media(510px) {
  .chooseUs__headTxt {
    max-width: 40rem;
  }
}
@include media(340px) {
  .chooseUs__headTxt {
    font: 4.8rem/6.7rem $raleway-bold, sans-serif;
  }
}
/** Process **/
.process {
  background: rgba(159, 201, 231, 0.21);
  padding: 10.6rem 0 0;
  position: relative;
  &::after {
    position: absolute;
    background: url("../img/svg/PROCESS.svg") no-repeat;
    height: 85.9rem;
    width: 13.7rem;
    background-size: cover;
    content: "";
    top: 50%;
    left: 7rem;
    transform: translate(-50%, -50%);
  }
  &__headTxt {
    color: $theme--dark--blue;
    font: 6.4rem/8.8rem $raleway-bold, sans-serif;
    max-width: 94rem;
    position: relative;
    margin-bottom: 8rem;
    &::after {
      position: absolute;
      content: "";
      width: 14rem;
      height: 7px;
      border-radius: 8px;
      background-color: rgba(66, 131, 197, 0.6);
      left: 50%;
      top: 110%;
      transform: translate(-50%, -50%);
    }
  }
}
.timeline-content-txt__readMore {
  @extend .iconBox-txts__readMore;
}
@include media(1199px) {
  .process:after {
    display: none;
  }
}
@include media(575px) {
  .process {
    padding: 6.4rem 0 0;
  }
  .process__headTxt {
    max-width: 36rem;
    font: 4.8rem/7.2rem $raleway-bold, sans-serif;
  }
}
@include media(414px) {
  .process__headTxt {
    max-width: 30rem;
    font: 4rem/7.2rem $raleway-bold, sans-serif;
  }
}
/** CTA **/
.cta {
  background: url("../img/callbg.png") no-repeat bottom center;
  background-size: cover;
  height: 100%;
  &-imgWrap {
    img {
      max-width: 48.6rem;
      padding-right: 3.6rem;
      margin-top: -4rem;
      position: relative;
      z-index: 1;
    }
  }
  &-textWrap {
    max-width: 82rem;
    margin-left: auto;
    display: flex;
    align-items: center;
    height: 100%;
    h6 {
      font: 3.6rem/6rem $sen-regular, sans-serif;
      color: $theme--dark--blue;
      position: relative;
      padding-top: 3rem;
      padding-bottom: 3rem;
      &::after,
      &::before {
        position: absolute;
        content: "";
        width: 5.3rem;
        height: 5.3rem;
      }
      &::after {
        right: 10rem;
        bottom: -1rem;
        background: url("../img/svg/right-quotes.svg") no-repeat;
      }
      &::before {
        top: -4.5rem;
        left: -3.5rem;
        background: url("../img/svg/left-quotes.svg") no-repeat;
      }
      span {
        font: bold 6.4rem/8rem $sen-bold, sans-serif;
      }
    }
  }
}
@include media(1439px) {
  .cta-textWrap {
    max-width: 65rem;
  }
}
@include media(1199px) {
  .cta-textWrap {
    max-width: 65rem;
    margin-right: auto;
    margin-top: 6.4rem;
  }
  .cta-imgWrap img {
    margin-bottom: 0;
    padding-right: 0;
    margin-left: auto;
    margin-right: auto;
    display: block;
  }
  .cta-textWrap h6:after {
    right: 42rem;
    bottom: 1rem;
  }
  .cta-textWrap h6 {
    padding-top: 2rem;
  }
}
@include media(767px) {
  .cta-textWrap h6 {
    font-size: 3.2rem;
    span {
      font-size: 5.6rem;
    }
  }
  .cta-textWrap h6:before {
    left: -0.5rem;
  }
  .cta-textWrap h6:after {
    right: 1rem;
  }
}
@include media(575px) {
  .cta-imgWrap img {
    max-width: 100%;
  }
  .cta-textWrap h6 {
    font-size: 2.8rem;
    span {
      font-size: 5.2rem;
    }
  }
  .cta-textWrap h6:before {
    left: 0;
  }
  .cta-textWrap h6:after {
    right: 0.5rem;
  }
}
/** CONNECT WITH US **/
.connectUs {
  &-map {
    height: 100%;
    width: 100%;
  }
  &-Info {
    max-width: 49rem;
    margin: 7rem auto;
    &-txtWrap {
      max-width: 39rem;
      position: relative;
      margin-bottom: 4rem;
      margin-right: auto;
      span {
        position: absolute;
        text-transform: uppercase;
        -webkit-text-stroke: 1px rgba(0, 0, 0, 0.1);
        color: #fff;
        font: 7.2rem/11.2rem $raleway-bold, sans-serif;
        z-index: -1;
        top: 0.1rem;
        width: 100%;
        text-align: center;
      }
      h6 {
        font: 4.8rem/6.8rem $raleway-bold, sans-serif;
        color: $theme--dark--blue;
        text-transform: capitalize;
        padding-top: 4.8rem;
        position: relative;
        &:after {
          position: absolute;
          content: "";
          background: #e5f2ff;
          width: 13.9rem;
          height: 5px;
          border-radius: 5px;
          bottom: -1.6rem;
          right: 0;
        }
      }
    }
    &-bodyTxt {
      margin-bottom: 4rem;
      color: $theme--light--black;
      p {
        font: 1.6rem/3.2rem $sen-regular, sans-serif;
      }
    }
    &-address {
      margin-bottom: 2.4rem;
      ul > li + ul {
        margin-left: 2.6rem;
      }
      &:last-child {
        margin-bottom: 0;
      }
      &__head {
        display: flex;
        margin-bottom: 1.6rem;
        img {
          max-width: 2.2rem;
          margin-right: 0.8rem;
        }
        p {
          color: $theme--black;
          font: 2.4rem/3.2rem $raleway-bold, sans-serif;
        }
      }
      li {
        color: $theme--light--black;
        font: 1.6rem/3.2rem $sen-regular, sans-serif;
        a {
          color: $theme--light--black;
          font: 1.6rem/3.2rem $sen-regular, sans-serif;
        }
      }
    }
  }
}
@include media(1399px) {
  .connectUs-Info {
    max-width: 39rem;
  }
}
@include media(1199px) {
  .connectUs-map {
    min-height: 60rem;
  }
}
@include media(575px) {
  .connectUs-Info-txtWrap span {
    font-size: 6rem;
  }
  .connectUs-Info-txtWrap h6 {
    text-align: center;
    max-width: 20rem;
    margin-left: auto;
    margin-right: auto;
  }
  .connectUs-Info {
    margin: 3rem 0;
  }
}
