/********** RESET CSS ***********/
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  overflow-x: hidden;
}

header {
  position: fixed;
  width: 100%;
  z-index: 99999;
  top: 0;
}

a {
  text-decoration: none !important;
}

ul {
  list-style-type: none;
  padding-left: 0;
  margin-bottom: 0;
}

select {
  -moz-appearance: none;
  -webkit-appearance: none;
}

select::-ms-expand {
  display: none;
}

p {
  margin-bottom: 0;
}

.btn.focus,
.btn:focus,
button:focus {
  outline: none;
  box-shadow: none;
}

.navbar-toggler:focus {
  box-shadow: none;
}

@media screen and (min-width: 1300px) {
  .container {
    max-width: 120rem;
  }
}
@media screen and (min-width: 1440px) {
  .container {
    max-width: 130rem;
  }
}
.mt--64 {
  margin-top: 6.4rem;
}

.whitespace--normal {
  white-space: normal !important;
}

.height--232 {
  max-height: 100% !important;
}

