/********* RETURN TO TOP *********/
#return-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background: #8CC0EC;
  width: 50px;
  height: 50px;
  display: block;
  text-decoration: none;
  border-radius: 35px;
  display: none;
  -webkit-transition: all 0.3s linear;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  z-index: 99;
  padding-top: 8px;
  text-align: center;
  color: #ffffff;
  font-size: 2.1rem;
  box-shadow: 0px 1px 6px rgba(51, 60, 127, 0.33);
}
#return-to-top:hover {
  background: #346CA4;
  color: #ffffff;
}

/****** HAMBURGER ICON *****/
.bar1,
.bar2,
.bar3 {
  width: 30px;
  height: 2px;
  background-color: #346CA4;
  margin: 0 0 6px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 2px;
}

.bar2 {
  width: 20px;
}

.change .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
  transform: rotate(-45deg) translate(-9px, 6px);
}

.change .bar2 {
  opacity: 0;
}

.change .bar3 {
  -webkit-transform: rotate(45deg) translate(-5px, -3px);
  transform: rotate(45deg) translate(-5px, -3px);
}

/********* NAV CLOSE *********/
.d--none {
  display: none !important;
}

.icon-bar {
  transition: 0.3s ease-in-out;
}

.rotate--45 {
  transform: rotate(45deg);
}

.rotate--neg-45 {
  transform: rotate(-45deg);
  margin-top: -2px !important;
}

/********* NAV EFFECT *********/
.bg--black {
  background-color: rgba(51, 51, 51, 0.878);
  transition: all 0.6s ease-in-out;
}

