.timeline-container {
  width: 100%;
  padding: 50px 0;
  margin: 50px auto;
  position: relative;
  overflow: hidden;
}

.timeline-container:before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -5px;
  width: 9px;
  height: 83%;
  background: #4283C5;
  z-index: 1;
  border-radius: 6px;
}

.timeline-block {
  width: -webkit-calc(50% + 15px);
  width: -moz-calc(50% + 15px);
  width: calc(50% + 15px);
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  clear: both;
  margin-bottom: 4rem;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 3.2rem;
  height: 3.2rem;
  border-radius: 50%;
  border: 8px solid #569be0;
  background: #eaf3f9;
  margin-top: 10px;
  z-index: 9999;
}

.timeline-content {
  width: 100%;
}

.timeline-content h3 {
  margin-top: 5px;
  margin-bottom: 5px;
  font-size: 25px;
  font-weight: 500;
}

.timeline-content span {
  direction: ltr;
}

/*Customisation*/
.timeline-content {
  position: relative;
}
.timeline-contentBox {
  max-width: 42.5rem;
  padding: 2.6rem;
  background: #ffffff;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.17);
  border-radius: 8px;
  margin-top: 1rem;
}
.timeline-content-img {
  max-width: 6rem;
  margin-bottom: 2.4rem;
}
.timeline-content-img img {
  width: 100%;
}
.timeline-content__outlineImg {
  max-width: 12.8rem;
  position: absolute;
  right: -1.6rem;
  top: -2rem;
}
.timeline-content-txt h3 {
  font: 2.4rem/3.2rem Raleway-Bold, sans-serif;
  color: #4283C5;
  margin-bottom: 1.6rem;
  text-transform: uppercase;
  word-break: break-all;
}
.timeline-content-txt p {
  font: 1.6rem/3.2rem Sen-Regular, sans-serif;
  color: #777777;
}

.timeline-block-left .timeline-content-img,
.timeline-block-right .timeline-content-img {
  margin-right: auto;
}
.timeline-block-left .timeline-content-txt h3,
.timeline-block-right .timeline-content-txt h3 {
  text-align: left;
}
.timeline-block-left .timeline-content-txt p,
.timeline-block-right .timeline-content-txt p {
  direction: ltr;
  text-align: left;
}

.timeline-block-left .marker {
  margin-left: 3.2rem;
}

.timeline-block-right .marker {
  margin-right: 3.2rem;
}

@media screen and (max-width: 991px) {
  .timeline-container:before {
    left: 0.8rem;
    width: 0.9rem;
  }

  .timeline-block-left .marker {
    margin-right: 3.2rem;
    margin-left: 0;
  }

  .timeline-container:before {
    left: 16px;
  }

  .timeline-block {
    width: 100%;
    margin-bottom: 3rem;
  }

  .timeline-block-right {
    float: none;
  }

  .timeline-block-left {
    float: none;
    direction: ltr;
  }

  .timeline-contentBox {
    max-width: 37rem;
  }

  .timeline-container {
    width: 65%;
  }
}
@media screen and (max-width: 767px) {
  .timeline-container {
    width: 100%;
  }
}
@media screen and (max-width: 575px) {
  .marker {
    width: 3rem;
    height: 3rem;
    border: 6px solid #569be0;
  }

  .timeline-container:before {
    left: 1rem;
    width: 0.6rem;
  }

  .timeline-block-left .marker {
    margin-right: 1.6rem;
  }

  .timeline-block-right .marker {
    margin-right: 1.6rem;
  }

  .timeline-container:before {
    left: 1.7rem;
  }

  .timeline-content-txt h3 {
    font-size: 2rem;
  }

  .timeline-contentBox {
    max-width: 42rem;
    flex-grow: 2;
  }

  .timeline-container:before {
    height: 82%;
  }

  .timeline-container {
    max-width: 48rem;
    margin-left: auto;
    margin-right: auto;
  }
}
@media screen and (max-width: 500px) {
  .timeline-contentBox {
    padding: 1.6rem;
    max-width: 35rem;
  }

  .timeline-container {
    max-width: 40rem;
  }

  .timeline-container:before {
    height: 81%;
  }
}
@media screen and (max-width: 414px) {
  .timeline-contentBox {
    max-width: 26rem;
  }

  .timeline-content-txt h3 {
    font-size: 1.6rem;
    margin-bottom: 1.4rem;
  }

  .timeline-content-txt p {
    font-size: 1.4rem;
  }

  .timeline-container {
    max-width: 31.5rem;
  }

  .timeline-container:before {
    height: 80%;
  }
}
